import * as _ from "lodash";

const clickEvent = () => {
  let callbacks = [];
  let hovers = [];

  const start = e => {
    const point = { x: e.pageX, y: e.pageY };
  };

  const move = e => {
    const point = { x: e.pageX, y: e.pageY };
    _.map(hovers, callback => {
      // console.log("hover", point);
      const isHit = getRectHit(callback.rect, point);
      if (isHit) {
        callback.callback(true);
        return;
      } else {
        callback.callback(false);
      }
    });
  };

  const end = e => {
    const point = { x: e.pageX, y: e.pageY };
    _.map(callbacks, callback => {
      // console.log(
      //   "click",
      //   callback.rect,
      //   point,
      //   document.scrollingElement.scrollTop
      // );
      const isHit = getRectHit(callback.rect, point);
      if (isHit) {
        callback.callback(true);
        return;
      } else {
        callback.callback(false);
      }
    });
  };

  const init = () => {
    console.log("events init");
    window.addEventListener("mousedown", start);
    window.addEventListener("touchstart", start);
    window.addEventListener("mousemove", move);
    window.addEventListener("touchmove", move);
    window.addEventListener("mouseup", end);
    window.addEventListener("touchend", end);
  };

  const setCallback = ({ id, rect, callback }) => {
    const isCallback = _.find(callbacks, callback => {
      return callback.id === id;
    });
    if (!isCallback) {
      console.log("setCallback", id);
      callbacks.push({ id, rect, callback });
    }
  };

  const setHover = ({ id, rect, callback }) => {
    const isCallback = _.find(hovers, callback => {
      return callback.id === id;
    });
    if (!isCallback) {
      console.log("setHover", id);
      hovers.push({ id, rect, callback });
    }
  };

  const removeCallback = id => {
    _.remove(callbacks, callback => {
      return callback.id === id;
    });
  };

  const getRectHit = (rect, point) => {
    //pointy
    const scrollTop = document.scrollingElement.scrollTop;
    if (
      rect.x < point.x &&
      rect.y < point.y - scrollTop &&
      rect.y + rect.height > point.y - scrollTop &&
      rect.x + rect.width > point.x
    ) {
      // console.log("area", rect, point, true);
      return true;
    }
    // console.log("area", rect, point, false);
    return false;
  };

  return {
    init,
    setCallback,
    setHover,
    removeCallback
  };
};

export const Events = clickEvent();
Events.init();
