import * as _ from "lodash";
import { pixelRatio } from "@/config";
export const SkewedRightReverse = (
  ref,
  srcRef,
  event,
  { top, value, diff, scrollY, drawRect }
) => {
  const ctxSrc = srcRef.getContext("2d");
  const rect = srcRef.getBoundingClientRect();
  rect.width = rect.width * pixelRatio;
  rect.height = rect.height * pixelRatio;
  const src = ctxSrc.getImageData(0, 0, rect.width, rect.height);
  let dst = ctxSrc.createImageData(rect.width, rect.height);

  //
  for (let y = 0; y < rect.height; y++) {
    for (let x = 0; x < rect.width; x++) {
      const i = Math.floor(y * (rect.width * 4) + x * 4);
      dst.data[i] = src.data[i];
      dst.data[i + 1] = src.data[i + 1];
      dst.data[i + 2] = src.data[i + 2];
      dst.data[i + 3] = src.data[i + 3];
    }
  }

  //1px
  // const pixRate = +(rect.width / (event.to - event.from)).toFixed(2);
  const pixRate = +(drawRect.width / (event.to - event.from)).toFixed(2);
  let last = [];
  let col = [];
  for (let y = 0; y < rect.height; y++) {
    for (let x = rect.width; x > 0; x--) {
      const i = Math.floor(y * (rect.width * 4) + x * 4);
      const edge_index = Math.floor(
        rect.width - Math.floor(drawRect.x + value * pixRate)
      );
      if (x === edge_index) {
        col[y] = {
          r: src.data[i],
          g: src.data[i + 1],
          b: src.data[i + 2],
          a: src.data[i + 3]
        };
        last[y] = { index: i, x, y };
      }
    }
  }

  for (let y = 0; y < rect.height; y++) {
    for (let x = 0; x < rect.width; x++) {
      const i = Math.floor(y * (rect.width * 4) + x * 4);
      const _y = Math.floor((y + scrollY) * (rect.width * 4) + x * 4);
      if (last[y] && x >= last[y].x) {
        dst.data[_y] = col[y].r;
        dst.data[_y + 1] = col[y].g;
        dst.data[_y + 2] = col[y].b;
        dst.data[_y + 3] = col[y].a;
      } else {
        dst.data[_y] = src.data[i];
        dst.data[_y + 1] = src.data[i + 1];
        dst.data[_y + 2] = src.data[i + 2];
        dst.data[_y + 3] = src.data[i + 3];
      }
    }
  }

  ctxSrc.putImageData(dst, rect.x, rect.y);
  const ctx = ref.getContext("2d");
  ctx.drawImage(
    srcRef,
    0,
    0,
    rect.width,
    rect.height,
    0,
    0,
    rect.width,
    rect.height
  );

  //
  // ctx.strokeStyle = "blue";
  // ctx.strokeRect(
  //   drawRect.x,
  //   drawRect.y + scrollY,
  //   drawRect.width,
  //   drawRect.height
  // );
};
