import * as _ from "lodash";
import { eventProvider } from "../../interection/eventProvider";
import * as eff from "../Effecter/Eff/Functions";
import { imageLoader } from "../../util/imageLoader";
import { imageStore } from "../../util/store";

const POSITION = {
  LEFT: "left",
  CENTER: "center",
  RIGHT: "right"
};

const FillImage = () => {
  let canvas;
  let drawRect;
  let imageData;

  const init = ref => {
    canvas = ref;
  };
  const setImage = (
    imageObj,
    { x, y, width, height, moveTo },
    { top, value, diff },
    maxWidth
  ) => {
    const src = { x: 0, y: 0, width: imageObj.width, height: imageObj.height };
    let visible = { x, y, width, height };

    //
    if (height === "auto") {
      visible.height = Math.floor(imageObj.height * (width / imageObj.width));
    }
    if (x === "auto") {
      visible.x = Math.floor(canvas.width / 2) - Math.floor(width / 2);
    }
    if (y === "auto") {
      visible.y =
        Math.floor(canvas.height / 2) - Math.floor(visible.height / 2);
    }
    if (moveTo.x) {
      visible.x = moveTo.x;
    }
    if (moveTo.y) {
      visible.y = moveTo.y;
    }

    drawRect = {
      x: visible.x,
      y: visible.y,
      width: visible.width,
      height: visible.height
    };

    const ctx = canvas.getContext("2d");
    ctx.drawImage(
      imageObj,
      src.x,
      src.y,
      src.width,
      src.height,
      visible.x,
      visible.y,
      visible.width,
      visible.height
    );

    //imageData 
    imageData = ctx.getImageData(
      drawRect.x,
      drawRect.y,
      drawRect.width,
      drawRect.height
    );
  };

  //
  const redraw = ref => {
    const ctx = ref.getContext("2d");
    ctx.clearRect(0, 0, ref.width, ref.height);
    ctx.putImageData(imageData, drawRect.x, drawRect.y);
  };

  //
  const getDrawRect = () => {
    return drawRect;
  };

  return {
    init,
    setImage,
    redraw,
    getDrawRect
  };
};

export const CanvasImage = (ref, srcRef, events, maxWidth) => {
  let moveUpdated = {
    x: 0,
    y: 0
  };

  //canvas
  const ctxSrc = srcRef.getContext("2d");
  const ctx = ref.getContext("2d");

  //
  eventProvider(
    events,
    //
    async (event, { top, value, diff }) => {
      //
      const moveperscroll =
        (event.moveFrom.y - event.moveTo.y) / Math.abs(event.to - event.from);
      moveUpdated.y += diff * moveperscroll;

      //
      let img;
      if (!imageStore.get(event.id)) {
        //
        img = FillImage();
        img.init(srcRef);
        let style = _.cloneDeep(event.rect);
        style.moveTo = {
          x: event.moveTo.x,
          y: event.moveFrom.y
        };

        //
        const imageObj = await imageLoader(event.img);
        img.setImage(imageObj, style, { top, value, diff }, maxWidth);
        imageStore.set(event.id, img);
      } else {
        img = imageStore.get(event.id);
        img.redraw(srcRef);
      }

      //dom
      const scrollY = Math.floor(moveUpdated.y);
      const drawRect = img.getDrawRect();

      // ctx.strokeStyle = "red";
      // ctx.strokeRect(drawRect.x, drawRect.y, drawRect.width, drawRect.height);

      //
      if (event.eff && event.eff.length) {
        _.map(event.eff, effect => {
          eff[effect](ref, srcRef, event, {
            top,
            value,
            diff,
            scrollY,
            drawRect
          });
        });
      }
    },
    //
    () => {
      //
      ctxSrc.clearRect(0, 0, srcRef.width, srcRef.height);
      ctx.clearRect(0, 0, ref.width, ref.height);
    }
  );
};
