import * as _ from "lodash";
import { pixelRatio } from "@/config";
import { eventProvider } from "../../interection/eventProvider";
import * as eff from "../Effecter/Eff/Functions";
import { textStore } from "../../util/store";
import { Events } from "../../util/events";

const FillText = () => {
  let canvas;
  let imageData = null;
  let textRect = {};
  let drawRect = {};

  const init = ref => {
    canvas = ref;
  };
  const setText = (
    text,
    { fontFamily, fontSize, color, align, moveTo, letterSpacing },
    { top, value, diff },
    maxWidth
  ) => {
    canvas.style.letterSpacing = letterSpacing;
    const ctx = canvas.getContext("2d");
    ctx.font = `${fontSize}px '${fontFamily}'`;
    ctx.fillStyle = color;
    ctx.textAlign = align;
    const _x =
      align === "center" ? Math.floor((maxWidth - moveTo.x) / 2) : moveTo.x;

    //
    const canvasRect = canvas.getBoundingClientRect();
    textRect = {
      x: canvasRect.width,
      y: 0,
      width: 0,
      height: 0
    };
    _.map(text, (item, index) => {
      const _y = moveTo.y - fontSize * 1.5 * index;
      const _maxwidth = maxWidth ? maxWidth - moveTo.x : null;
      ctx.fillText(
        _.isFunction(item) ? item({ top, value, diff }) : item,
        _x,
        _y,
        _maxwidth
      );

      ///
      textRect.x = moveTo.x;
      textRect.y = moveTo.y - fontSize * 1.5 * (index + 1);
      textRect.width = _maxwidth > textRect.width ? _maxwidth : textRect.width;
      textRect.height = fontSize * 1.5 * (index + 1) + fontSize;
    });

    //imageData 
    const rect = canvas.getBoundingClientRect();
    rect.width = rect.width * pixelRatio;
    rect.height = rect.height * pixelRatio;
    imageData = ctx.getImageData(
      textRect.x,
      textRect.y,
      textRect.width,
      textRect.height
    );

    //
    let positionX = { min: textRect.width, max: 0 };

    for (let y = 0; y < textRect.height; y++) {
      for (let x = 0; x < textRect.width; x++) {
        const i = Math.floor(y * (textRect.width * 4) + x * 4);
        const col =
          (imageData.data[i] + imageData.data[i + 1] + imageData.data[i + 2]) /
          3;
        if (imageData.data[i + 3] >= 1 && col < 128) {
          positionX.min = x < positionX.min ? x : positionX.min;
          positionX.max = x > positionX.max ? x : positionX.max;
        }
      }
    }

    //
    drawRect = {
      x: positionX.min,
      y: textRect.y,
      width: positionX.max - positionX.min,
      height: textRect.height
    };

    //
    // ctx.strokeStyle = "blue";
    // ctx.strokeRect(drawRect.x, drawRect.y, drawRect.width, drawRect.height);
    console.log("positionX", drawRect);
  };

  //
  const redraw = ref => {
    const ctx = ref.getContext("2d");
    ctx.clearRect(0, 0, ref.width, ref.height);
    ctx.putImageData(imageData, textRect.x, textRect.y);
  };

  //
  const getDrawRect = () => {
    return drawRect;
  };

  return {
    init,
    setText,
    redraw,
    getDrawRect
  };
};

export const CanvasText = (ref, srcRef, events, maxWidth) => {
  let moveUpdated = {
    x: 0,
    y: 0
  };

  //canvas
  const ctxSrc = srcRef.getContext("2d");
  const ctx = ref.getContext("2d");

  //
  eventProvider(
    _.cloneDeep(events),
    //
    (event, { top, value, diff }) => {
      //
      const moveperscroll =
        (event.moveFrom.y - event.moveTo.y) / Math.abs(event.to - event.from);
      moveUpdated.y += diff * moveperscroll;

      //
      let text;
      if (!textStore.get(event.id)) {
        //
        text = FillText();
        text.init(srcRef);
        const style = _.cloneDeep(event.style);
        style.moveTo = {
          x: event.moveTo.x,
          y: event.moveFrom.y
        };
        const _text = _.cloneDeep(event.text).reverse();
        text.setText(_text, style, { top, value, diff }, maxWidth);
        textStore.set(event.id, text);
      } else {
        text = textStore.get(event.id);
        text.redraw(srcRef);
      }

      //dom
      const scrollY = Math.floor(moveUpdated.y);
      const drawRect = text.getDrawRect();

      // ctx.strokeStyle = "blue";
      // ctx.strokeRect(drawRect.x, drawRect.y, drawRect.width, drawRect.height);

      //
      const eventRect = {
        x: drawRect.x,
        y: drawRect.y + scrollY,
        width: drawRect.width,
        height: drawRect.height
      };

      //

      if (event.link) {
        //hover
        Events.setHover({
          id: event.id,
          rect: eventRect,
          callback: status => {
            if (status) {
              ref.classList.add("hover");
              console.log("hover", event.id, status);
            } else {
              ref.classList.remove("hover");
            }
          }
        });

        //
        Events.setCallback({
          id: event.id,
          rect: eventRect,
          callback: status => {
            console.log("event", event.id, status);
          }
        });

        //
        ctx.strokeStyle = "blue";
        ctx.strokeRect(
          eventRect.x,
          eventRect.y,
          eventRect.width,
          eventRect.height
        );
      }

      //
      if (event.eff && event.eff.length) {
        _.map(event.eff, effect => {
          eff[effect](ref, srcRef, event, {
            top,
            value,
            diff,
            scrollY,
            drawRect
          });
          //STAY
        });
      }
    },
    //
    () => {
      //
      ctxSrc.clearRect(0, 0, srcRef.width, srcRef.height);
      ctx.clearRect(0, 0, ref.width, ref.height);
    }
  );
};
