import * as _ from "lodash";
import { ContextStore } from "../context/Context";

const _scroller = () => {
  let callbacks = [];
  let _document = document.getElementsByTagName("body")[0];
  let height = _document.getBoundingClientRect().height;
  let scrollTop = 0;

  document.addEventListener("scroll", () => {
    // console.log("scrolling", document.scrollingElement);
    const top = document.scrollingElement.scrollTop;
    const params = { top };

    //context
    ContextStore.setContext("SCROLL", params);

    //
    _.map(callbacks, callback => {
      callback(params);
    });
  });

  const setHeight = n => {
    height += n;
    _document.style.height = height + "px";
  };

  const getScroll = callback => {
    callbacks.push(callback);
  };

  const enable = n => {
    height += n;
    _document.style.height = height + "px";
    if (scrollTop) {
      window.scrollTo({
        top: scrollTop,
        behavior: "smooth"
      });
    }
  };

  const disable = () => {
    // console.log(document.scrollingElement.scrollTop);
    scrollTop = document.scrollingElement.scrollTop;
    _document.style.height = "auto";
  };

  return {
    setHeight,
    getScroll,
    disable,
    enable
  };
};

export const scroller = _scroller();
